<template>
  <div class="home-container">
    <!-- 头部区域 -->
    <!-- <van-nav-bar title="黑马头条" :fixed="true" /> -->
    <van-nav-bar title="黑马头条" fixed />

    <!-- 头条 内容区域 导入，注册，使用 ArtItem组件-->
    <!-- 在使用组件的时候，如果某个属性名是‘小驼峰’，则绑定属性的时候，建议改成连字符格式 -->
    <van-pull-refresh v-model="isloading" :disabled="finished" @refresh="onRefresh">
      <van-list v-model="loading" :finished="finished" finished-text="-我们也是有底线的-" @load="onLoad">
        <ArtItem v-for="item in artlist" :key="item.id" :title="item.title" :author="item.aut_name"
          :comm-count="item.comm_count" :time="item.pubdate" :cover="item.cover"></ArtItem>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import ArtItem from '@/components/ArtItem.vue'
// 导入requset.js
// import request from '@/utils/request.js'

import { getArticleListAPI } from '@/api/articleAPI.js'

// const result = getArticleListAPI(1,10)
// console.log(result)

export default {
  name: 'MyHome',
  data() {
    return {
      page: 1,
      limit: 10,
      artlist: [],
      // loading 是否正在加载下一页数据 为true 不会触发load事件，请求完数据每次需要手动改为false
      // finished 所以数据加载完毕，为true则加载完毕
      loading: true,
      finished: false,
      // 下拉刷新，加载上一页数据
      isloading: false
    }
  },
  created() {
    this.initAtricleList()
  },
  methods: {
    // 封装获取文章列表数据的方法
    async initAtricleList(isRefresh) {
      // 发起get请求，获取列表数据
      const { data: res } = await getArticleListAPI(this.page, this.limit)
      // console.log(res)

      if (isRefresh) {
        // 证明是下拉刷新： [新数据在前，旧数据在后]
        this.artlist = [...res, ...this.artlist]
        this.isloading = false
      } else {
        // 证明是上拉刷新
        // 如果上拉加载更多，那么
        // this.artlist = [旧数据在前，新数据在后]
        this.artlist = [...this.artlist, ...res]
        this.loading = false
      }

      // 如果res.length === 0 证明没有下一页了
      if (res.length === 0) {
        // 直接把finished改为true
        this.finished = true
      }
    },
    onLoad() {
      // onLoad 被调用，就请求下一页数据
      console.log('触发onload')
      // 1.触发后，让页面值+1
      this.page++
      // 2.重新请求接口
      this.initAtricleList()
    },
    onRefresh() {
      // onRefresh被调用，就请上一页数据
      console.log('触发下拉刷新')
      // 1.触发后，让页面值+1
      this.page++
      // 2.重新请求接口
      this.initAtricleList(true)
    }
  },
  components: {
    ArtItem
  }
}
</script>

<style lang="less" scoped>
.home-container {
  padding: 46px 0 50px 0;

  .van-nav-bar {
    background-color: #007bff;
  }

  /deep/ .van-nav-bar__title {
    color: #fff;
  }
}
</style>
